import React, { useState } from "react";

import "./NavBar.css";

function NavBar({ mobileMenu, showMenu }) {
  function handleClick() {
    mobileMenu();
    // document.body.style.overflow = "hidden";
  }

  const styledBarOne = {
    top: "4px",
    transform: "rotate(45deg)",
    transformOrigin: "center center",
    width: "20px",
  };

  const styledBarThree = {
    top: "4px",
    transform: "rotate(-45deg)",
    transformOrigin: "center center",
    width: "20px",
  };

  return (
    <nav>
      <div className="navigation">
        <div className="logo">
          <h2>Karthik Bethi</h2>
        </div>
        <div onClick={handleClick} className="mobile-menu">
          <div className="bar-one" style={showMenu ? styledBarOne : null}></div>
          <div
            className="bar-two"
            style={
              showMenu
                ? {
                    display: "none",
                  }
                : null
            }
          ></div>
          <div
            className="bar-three"
            style={showMenu ? styledBarThree : null}
          ></div>
        </div>
      </div>
      <div className="navigation-stick">
        <div
          className="mobile-navigation"
          style={{ transform: showMenu ? "scale(1)" : null }}
        >
          <ul className="nav-links">
            <li>
              <a href="#about-nav">About</a>
            </li>
            <li>
              <a href="#projects-nav"> Projects </a>
            </li>
            <li>
              <a href="#contact-nav"> Contact </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default NavBar;
