import React, { useEffect } from "react";
import "./Contact.css";
import AOS from "aos";
import "aos/dist/aos.css";

function Contact() {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);
  return (
    <section data-aos="fade-right" id="contact-nav" className="contact">
      <div className="contact-container">
        <h4 data-aos="fade-right" data-aos-duration="700">
          What's Next?
        </h4>
        <h2 data-aos="fade-left" data-aos-duration="800">
          Get In Touch
        </h2>
        <p data-aos="fade-right" data-aos-duration="900">
          I'm currently looking for new opportunities, my inbox is always open.
          If you have any questions or just want to say hi, please drop me an
          email!
        </p>
        <div className="contact-container-button">
          <a
            data-aos="fade-left"
            data-aos-duration="1000"
            href="mailto:karthikreddybethi@gmail.com"
          >
            Say Hello
          </a>
        </div>
      </div>
    </section>
  );
}

export default Contact;
