import React, { useEffect } from "react";
import "./AboutMe.css";
import ReactIcon from "../Images/atom.png";
import HtmlIcon from "../Images/html.png";
import CssIcon from "../Images/css.png";
import JsIcon from "../Images/js .png";
import Image from "../Images/karthikbethi.jpg";
import AOS from "aos";
import "aos/dist/aos.css";

function AboutMe() {
  useEffect(() => {
    AOS.init();
  }, []);
  return (
    <section className="about" id="about-nav">
      <div className="about-me">
        <div className="about-title" data-aos="fade-left">
          <h2>About Me</h2>
        </div>
        <div className="about-content" data-aos="fade-right">
          <p>
            {" "}
            Hello! I'm Karthik - front-end developer based in Gliwice, Poland.
            <br />
            <br />
            I enjoy creating things that live on the internet, whether it's a
            website or web application. My goal is to always build products that
            provide pixel-perfect, performant experiences.
            <br />
            <br />
            Here are technologies I've been working with recently:
          </p>
        </div>
        <div className="skills" data-aos="fade-left">
          <ul className="skills-list">
            <li className="skill-html">
              <img src={HtmlIcon} alt="html-icon" />
            </li>
            <li className="skill-css">
              <img src={CssIcon} alt="css-icon" />
            </li>
            <li className="skill-js">
              <img src={JsIcon} alt="" />
            </li>
            <li className="skill-react">
              <img src={ReactIcon} alt="react-icon" />
            </li>
          </ul>
        </div>
      </div>

      <div className="image" data-aos="fade-right">
        <img src={Image} alt="" />
        <div></div>
      </div>
    </section>
  );
}

export default AboutMe;
