import React, { useEffect } from "react";
import "./Main.css";
import AboutMe from "./AboutMe";
import Projects from "./Projects";
import Contact from "./Contact";
import AOS from "aos";
import "aos/dist/aos.css";
import Social from "./Social";

function Main({ hideMenu }) {
  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <main>
      <section
        onClick={() => {
          hideMenu(false);
        }}
        className="hero-container"
      >
        <div>
          <div
            className="hero-heading"
            data-aos="fade-left"
            data-aos-duration="500"
            data-aos-delay="160"
          >
            <p>Hi, my name is </p>
            <h1>Karthik Bethi.</h1>
            <h1 className="second-heading">I build things for the web.</h1>
          </div>
          <div
            className="hero-content"
            data-aos="fade-left"
            data-aos-delay="180"
            data-aos-duration="500"
          >
            <p>
              I am a front-end web developer based in Gliwice, Poland
              specializing in building (and often designing) exceptional
              websites.
            </p>
          </div>
          <div
            className="hero-button"
            data-aos="fade-left"
            data-aos-delay="200"
            data-aos-duration="500"
          >
            <a href="mailto:karthikreddybethi@gmail.com">
              <p>Get In Touch</p>
            </a>
          </div>
        </div>
      </section>
      <AboutMe />
      <Projects />
      <Contact />
      <Social />
    </main>
  );
}

export default Main;
