import React, { useEffect } from "react";
import Card from "./Card";
import CardData from "../data";
import AOS from "aos";
import "aos/dist/aos.css";
import "./Projects.css";

function Project() {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);
  return (
    <section id="projects-nav" data-aos="fade-right">
      <div className="projects-heading" data-aos="fade-right">
        <h2>Some Things I've Build</h2>
      </div>
      <div>
        <div className="card-container">
          {CardData.map(
            ({
              id,
              image,
              cardTitle,
              cardDescription,
              gitIcon,
              exportIcon,
              gitHubLink,
              webLink,
            }) => {
              return (
                <Card
                  id={id}
                  img={image}
                  title={cardTitle}
                  description={cardDescription}
                  gitIcon={gitIcon}
                  exportIcon={exportIcon}
                  gitHubLink={gitHubLink}
                  webLink={webLink}
                />
              );
            }
          )}
        </div>
      </div>
    </section>
  );
}

export default Project;
